.education {
    padding-top: 30%;
    padding-bottom: 15%;
    margin-left: 10%;
    margin-right: 15%;
    width: 100%;
}

#education {
    margin-left: 17%;
}

.education-first {
    width: 80%;
    margin-left: 15%;
}

li {
    list-style-type: none;
}

.first-name {
    font-size: larger;
}

.first-date {
    text-align: right;
    margin-right: 15px;
}

.text {
    padding-bottom: 10px;
    border-bottom: solid 3px black;
    width: 95%;
}

@media only screen and (min-device-width : 240px) and (max-device-width : 1200px) {
    .education {
        position: relative;
        margin-left: -10%;
        margin-top: 125%;
    }
    .education-first {
        position: relative;
        left: -10%;
        width: 120%;
        text-align: justify;
    }
}