a {
    color: #FF7129;
    text-decoration: none;
}

a::after {
    color: #FF7129;
}

.about {
    margin-left: 15%;
    z-index: 0;
}

.about-title {
    position: relative;
    font-size: 6rem;
    left: 35%;
    top: 15%;
    padding-top: 15%;
}

.about-title-first {
    color: #FF7129;
    z-index: 0;
}

.about-title-second {
    color: #2F2F2B;
    z-index: 0;
}

.mail {
    position: relative;
    margin-left: 35%;
}

.about-job {
    position: relative;
    left: 35%;
    margin-top: 0%;
    padding-top: 0%;
    color: #FF7129;
}

.logos {
    position: relative;
    margin-left: 60%;
    margin-top: -2%;
}

.linkedin, .github {
    width: 50px;
}

.description {
    font-size: large;
    color: #74746f;
    margin-left: 35%;
    margin-top: 2%;
}

@media only screen and (min-device-width : 240px) and (max-device-width : 1200px) {
    .about {
        margin-top: 25%;
        margin-left: 0%;
    }

    .about-title {
        position: relative;
        padding-top: 25%;
        left: 22%;
        padding: 0;
        margin-bottom: 25%;
    }

    .about-job {
        position: absolute;
        top: 40%;
        left: 22%;
    }

    .mail {
        position: absolute;
        top: 43%;
        left: -15%;
    }

    .about-title-first, .about-title-second {
        font-size: 50%;
    }

    .logos {
        position: absolute;
        display: flex;
        flex-direction: row;
        left: -25%;
        top: 50%;
    }

    .description {
        position: absolute;
        top: 55%;
        width: 100%;
        left: -35%;
        text-align: center;
        margin-bottom: 20%;
    }
}