.header {
    position: fixed;
    color: #FF7129;
    background-color: #2F2F2B;
    width: 15%;
    height: 100%;
}

.hamburger {
    display: none;
}

.header-list {
    position: relative;
    top: 35%;
    margin: 0;
    padding: 0;
    padding-top: 4%;
    width: 30%;
    list-style-type: none;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: space-between;
    font-size: larger;
    width: 100%;
}

.links {
    padding: 10px 10px;
}

.links:hover{
    color: #2F2F2B;
    background-color: #FF7129;
}

@media only screen and (min-device-width : 240px) and (max-device-width : 1200px) {
    .header {
        position: -webkit-sticky;
        position: fixed;
        top: 0;
        left: 0;
        color: #FF7129;
        background-color: #2F2F2B;
        width: 143%;
        height: 75px;
        z-index: 100;
    }

    .header-list {
        font-size: xx-large;
        position: -webkit-sticky;
        position: sticky;
        top: 75px;
        padding-left: 10px;
        text-align: left;
        background-color: #2F2F2B;
        width: auto;
        display: flex;
        flex-direction: column;
        visibility: hidden;
        z-index: 100;
    }

    .links:hover{
        color: #FF7129;
        background-color: #2F2F2B;
    }

    .hamburger {
        display: inline;
        width: 50px;
        margin-top: 10px;
        margin-left: 10px;
    }

    .about-title > * {
        overflow: auto;
        z-index: 5;
    }
}